
import { useEffect, useState } from 'react';
import todoService from './services/todos';

import Box from '@mui/material/Box';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import CheckIcon from '@mui/icons-material/Check';
import CloseIcon from '@mui/icons-material/Close';
import { Container, Divider, Typography } from '@mui/material';

function App () {
  
  const [ todos, setTodos ] = useState([ { todo: 'You need to do something', done: true } ])

  
  
  useEffect(() => {
    
  const getTodos = async () => {
     const allTodos = await todoService.getTodos()
      setTodos(allTodos)
  }
    getTodos() 
  },[])

  return (
    <Container>
      
      <Box sx={{ width: '100%', maxWidth: 900, height: 900, bgcolor: 'background.paper', backgroundImage: 'url(image.jpg)' }}>
        <ListItem>
          <Typography>Tasks</Typography>        
      </ListItem>
      <Divider/>
      {
        todos.map(item => {
          return (
      <nav key={item.todo}>
        <List>                  
          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
                {item.checked ? <CheckIcon color='primary'/> : <CloseIcon color='error'/>}
              </ListItemIcon>
              <ListItemText primary={item.todo} />
            </ListItemButton>
          </ListItem>          
        </List>
      </nav>  
          )
        })
        }
         </Box>
    </Container>
  );
}

export default App;
